import axios from '@/libs/api.request'

export const getNoticeTPL = (params) => {
    return axios.request({
        url: '/api/noc/v1/template',
        method: 'get',
        params
    })
}



export const optNoticeTPL = (data, method) => {
    return axios.request({
        url: '/api/noc/v1/template',
        method,
        data
    })
}

export const testNoticeTPL = (params, data) => {
    return axios.request({
        url: '/api/noc/v1/alert',
        method: 'post',
        params,
        data
    })
}
