<template>
  <div>
    <p class="label">回调配置:</p>
    <FormItem>
      <div
        v-for="(webhook, index) in webhooks"
        :key="index"
        class="webhook-item"
      >
        <Row :gutter="16">
          <Col span="7">
            <FormItem label="回调名称">
              <Input v-model="webhook.alias" placeholder="请输入回调名称" />
            </FormItem>
          </Col>
          <Col span="9">
            <FormItem label="URL">
              <Input v-model="webhook.url" placeholder="请输入回调URL" />
            </FormItem>
          </Col>
          <Col span="6">
            <FormItem label="回调情景">
              <Select v-model="webhook.mode" placeholder="请选择回调情景">
                <Option
                  v-for="item in callbackModeList"
                  :value="item.value"
                  :key="item.value"
                  >{{ item.label }}</Option
                >
              </Select>
            </FormItem>
          </Col>
          <Col span="2" style="margin-top: 28px">
            <Button
              type="text"
              @click="removeWebhook(index)"
              icon="md-close"
            ></Button>
          </Col>
        </Row>
      </div>
      <Button
        type="dashed"
        long
        icon="md-add"
        @click="addWebhook"
        style="margin-top: 10px"
        >添加回调配置</Button
      >
    </FormItem>
  </div>
</template>

<script>
import { callbackModeList } from '@/api/const'

export default {
  name: 'WebhookEdit',
  props: {
    value: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      callbackModeList,
      webhooks: []
    }
  },
  watch: {
    value: {
      handler (val) {
        this.webhooks = val || []
      },
      immediate: true
    },
    webhooks: {
      handler (val) {
        this.$emit('input', val)
      },
      deep: true
    }
  },
  methods: {
    addWebhook () {
      this.webhooks.push({
        url: '',
        alias: '',
        mode: 'AlertCallbackModeNone'
      })
    },
    removeWebhook (index) {
      this.webhooks.splice(index, 1)
    }
  }
}
</script>

<style lang="less" scoped>
.label {
  margin: 8px 0;
  font-size: 14px;
  font-weight: normal;
  color: #515a6e;
}

.webhook-item {
  margin-bottom: 16px;
  padding: 16px;
  background: #f8f8f9;
  border-radius: 4px;
}
</style>
