<template>
  <div ref="dom" class="charts chart-pie"></div>
</template>

<script>
import echarts from 'echarts'
import tdTheme from './theme.json'
import { on, off } from '@/libs/tools'
echarts.registerTheme('tdTheme', tdTheme)
export default {
  name: 'ChartPie',
  props: {
    value: Array,
    text: String,
    subtext: String
  },
  data () {
    return {
      dom: null
    }
  },
  methods: {
    pieConsole(param) {
      alert(param.dataIndex);
      //     param.dataIndex
      //     alert(param.value);
      //     alert(param.name);
      // 　　clickFunc(param.dataIndex);//执行点击效果,触发相应js函数
      //param具体包含的方法见 https://blog.csdn.net/allenjay11/article/details/76033232
      //刷新页面
      // location.reload();
      // window.location.reload();
    },
    resize () {
      this.dom.resize()
    },
    initPie () {
      this.$nextTick(() => {
        let legend = this.value.map(_ => _.name)
        let option = {
          title: {
            text: this.text,
            subtext: this.subtext,
            x: 'center'
          },
          tooltip: {
            trigger: 'item',
            formatter: '{b} : {c} ({d}%)'
          },
          legend: {
            orient: 'vertical',
            left: 'left',
            data: legend
          },
          series: [
            {
              type: 'pie',
              radius: '55%',
              center: ['50%', '60%'],
              data: this.value,
              label: {
                normal: {
                  show: true,
                  color: '#516B91',
                }
              },
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        };
        this.dom = echarts.init(this.$refs.dom, 'tdTheme');
        this.dom.setOption(option);
        this.dom.on("click", this.pieConsole);
        on(window, 'resize', this.resize)
      })
    }
  },
  mounted () {
    this.initPie()
  },
  beforeDestroy () {
    off(window, 'resize', this.resize)
  }
}
</script>
