<template>
  <Card :bordered="false" dis-hover>
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-colon='true'>
      <Row>
        <Col span="21" offset="2" style="padding: 15px 5px">
        <span style="color: #464c5b; font-size: 16px; font-weight: bold;">基础信息</span>
        </Col>

        <Col span="16" offset="4">
        <FormItem label="路由名称" prop="name" :label-width="85">
          <Input v-model="formValidate.name" maxlength="80" type="text" placeholder="请输入路由名称..."></Input>
        </FormItem>

        <FormItem label="路由状态" :label-width="85">
          <RadioGroup v-model="formValidate.status" type="button">
            <Radio label="yes">启用</Radio>
            <Radio label="no">禁用</Radio>
          </RadioGroup>
        </FormItem>

        <FormItem label="通知通道" prop="channel_id" :label-width="85">
          <Select v-model="formValidate.channel_id" filterable placeholder="请选择通知通道...">
            <Option v-for="item in channalList" :value="item.id" :key="item.id">{{ item.name }}</Option>
          </Select>
        </FormItem>


        <FormItem label="路由备注" prop="description" :label-width="85">
          <Input v-model="formValidate.description" maxlength="80" type="textarea"
            :autosize="{ minRows: 2, maxRows: 5 }" placeholder="请输入路由备注..."></Input>
        </FormItem>
        </Col>

        <Col span="21" offset="2" style="padding: 15px 5px">
        <p style="color: #464c5b; font-size: 16px; font-weight: bold;">触发条件</p>
        </Col>

        <Col span="16" offset="4" style="padding-right: 20px">
        <template v-for="items, i in formValidate.condition_list" class="alert-c">
          <FormItem :label="i | testF(i)" :label-width="85"
            v-if="formValidate.condition_list && formValidate.condition_list.length !== 0">
            <Alert show-icon v-if="i == 0" style="min-width:500px;max-width: 704px;  margin-bottom: 8px;">
              卡片之间关系为或，卡片内的条件关系为且。</Alert>
            <Row :gutter="20">
              <Col span="22">
              <Card v-if="items.length > 0" style="min-width:500px; margin-bottom: 0px;" dis-hover>

                <FormItem v-for="(item, index) in items" v-if="item.status" :key="index + _uid">
                  <div class="alert-con">
                    <!-- {{ index + 1 }} - <Input class="rule-input2" v-model="item.label" clearable :maxlength='25'
                      placeholder="输入标签" /> -->

                    {{ index + 1 }} -
                    <AutoComplete class="rule-input2" v-model="item.label" :data="autoData"
                      :filter-method="filterMethod" clearable :maxlength='50' :key="_uid" placeholder="输入标签" />

                    <Select v-model="item.operator" class="rule-select" filterable>
                      <Option v-for="item in operatorList" :value="item.value" :key="item.value">{{ item.label }}
                      </Option>
                    </Select>

                    <Input class="rule-input2" v-model="item.value" clearable :maxlength='25' placeholder="标签值" />

                    <Button shape="circle" type="text" icon="md-add" class="rule-btn-add"
                      @click="handleAdd(i)"></Button>
                    <Button v-if="items.length !== 1" shape="circle" type="text" icon="md-close" class="rule-btn-del"
                      @click="handleRemove(i, index)"></Button>

                  </div>
                </FormItem>
              </Card>
              <p v-if="formValidate.condition_list.length > 1 && i != 2" style="padding-top: 15px">或</p>
              </Col>
              <Col span="2">
              <a v-if="formValidate.condition_list.length !== 1" @click.prevent="handleRemoveCard(i)"> 删除</a>
              </Col>
            </Row>
          </FormItem>
        </template>
        </Col>

        <Col span="19" offset="4">
        <FormItem :label-width="85" style="padding: 0px 0px;">
          <Row :gutter="10">
            <Col span="12">
            <Button type="dashed" long @click="handleAddCard" icon="md-add">新增条件</Button>
            </Col>
            <Col v-if="formValidate.condition_list" span="11" style="margin-left: 0px;">
            <template>还可以添加{{ 3 - formValidate.condition_list.length }}个条件</template>
            </Col>
          </Row>
          <div style="padding-top: 12px;">
            <Button type="primary" :loading="btnLoading" @click="handleSubmit('formValidate')">确定</Button>
            <Button @click="handleCancel" style="margin-left: 15px">返回</Button>
          </div>
        </FormItem>

        </Col>
      </Row>
    </Form>

  </Card>
</template>
<script>
import { autoData } from '@/api/const'
export default {
  components: {},
  name: 'routeEdit',
  props: {
    formData: {
      type: Object,
      default: {}
    },
    channalList: {
      type: Array,
      default: []
    }
  },
  data () {
    return {
      index: 1,
      condition_items_num: 2,
      btnLoading: false,
      autoData,
      operatorList: [{ label: "等于", value: "==" }, { label: "不等于", value: "!=" },
      { label: "正则", value: "=~" }, { label: "正则取反", value: "!~" }],
      ruleValidate: {
        name: [{ required: true, message: '名称不能为空', trigger: 'blur' }],
        status: [{ required: true, message: '不能为空', trigger: 'change' }],
        channel_name: [{ required: true, type: 'number', message: '不能为空', trigger: 'change' }],
      }
    }
  },
  filters: {
    testF (value, location) {
      if (location === 1) {
        return '条件 B'
      } else if (location === 2) {
        return '条件 C'
      } else if (location === 0) {
        return '条件 A'
      }
    },
  },
  methods: {
    handleCancel () {
      if (JSON.stringify(this.formValidate) != JSON.stringify(this.formData)) {
        this.$Modal.confirm({
          content: `<p>编辑还没有保存，确定要离开？</p>`,
          onOk: () => {
            this.$emit('edit-close')
          },
          onCancel: () => { this.$Message.info('回头是岸'); }
        });
      } else {
        this.$emit('edit-close')
      }
    },
    //
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.btnLoading = true
          this.formValidate.name = this.formValidate.name.trim()
          setTimeout(() => {
            this.$emit('save-data', this.formValidate)
            this.btnLoading = false
          }, 1000)
        } else {
          this.$Message.error('表单校验不通过!');
        }
      })
    },
    //
    handleAdd (i) {
      this.index++;
      if (this.formValidate.condition_list[i].length > 9) {
        this.$Notice.warning({
          title: '限制提示',
          desc: '添加指标的个数不能超过十个',
          duration: 5
        });
        return
      }
      this.formValidate.condition_list[i].push({
        index: this.index,
        label: '',
        operator: '==',
        value: '',
        status: 1
      });
    },
    handleRemove (i, index) {
      this.formValidate.condition_list[i].splice(index, 1)
    },
    handleRemoveCard (i) {
      console.info(i)
      this.formValidate.condition_list.splice(i, 1)
    },
    handleAddCard () {
      if (this.formValidate.condition_list.length > 2) {
        this.$Notice.warning({
          title: '限制提示',
          desc: '条件组不能超过三个',
          duration: 5
        });
        return
      }
      this.formValidate.condition_list.push([{
        label: '',
        operator: '==',
        value: '',
        index: 1,
        status: 1
      }])
    },
    filterMethod (value, option) {
      return option.toUpperCase().indexOf(value.toUpperCase()) !== -1;
    },

  },
  watch: {
    formValidate (value) {
    },
  },
  computed: {
    formValidate: function () {
      return this.formData
    }
  },
  //
  mounted () {
  }
}
</script>

