<template>
  <div style="min-height: 100%; background:#f8f8f9;">
    <Card v-if="!showEdit" dis-hover :bordered="false">
      <div class="search-con search-con-top">
        <Button type="primary" class="search-btn" @click="handleAdd">创建消息模板</Button>
        <Input class="search-input" v-model="initPara.searchText" search @on-search="handleSearch" :maxlength='80'
          placeholder="输入关键字搜索" />

        <Button style="float: right; margin-right: 2px" icon="md-refresh" :loading="i.btnLoading"
          @click="handleRefresh"></Button>

      </div>
      <Table ref="selection" @on-sort-change="handleSort" @on-selection-change="handleSelect" :columns="columns"
        :data="tableData" :loading="i.tableLoading"></Table>

      <div style="margin: 10px; overflow: hidden">
        <div style="float: right;">
          <Page :total="i.pageTotal" :current="initPara.pageNum" :page-size="initPara.pageSize" show-total show-sizer
            show-elevator :page-size-opts="[10, 15, 50, 1000]" @on-change="handlerChangePage"
            @on-page-size-change="handlePageSize" />
        </div>
      </div>
    </Card>

    <tpl-edit v-else :formData="theData" @save-data="handleSaveData" @test-tpl="handleTestTPL"
      @edit-close="handleClose" />

  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
import { getNoticeTPL, optNoticeTPL, testNoticeTPL } from '@/api/notice/tpl'
import tplEdit from './components/tpl-edit'
import formatDate from '@/libs/util.js'
const tplTypeDict = {
  "fs": "飞书机器人", "fsapp": "飞书应用", "dd": "钉钉机器人", "ddapp": "钉钉应用",
  "wx": "企业微信机器人", "wxapp": "企业微信应用", "alydx": "阿里云短信", "alydh": "阿里云电话",
  "txdx": "腾讯云短信", "txdh": "腾讯云电话", "email": "邮件", "webhook": "Webhook"
}
export default {
  components: { tplEdit },
  data () {
    return {
      showEdit: false,
      action: 'post',
      //
      initPara: {
        pageNum: 1, // 当前页码
        pageSize: 10,  // 每页条数
        searchText: "",//查找值
        field: "",      //展示字段
        order: "descend", //排序 descend/ ascend
        searchField: "",  // 查找字段
        // custom: "all",
        cache: "yes",// 缓存
      },
      filter_map: {},
      initForm: {
        type: "fs",
        use: "tianshu"
      },
      i: {
        pageTotal: 0, // 数据总数
        tableLoading: true,
        btnDisabled: true,
        btnLoading: false
      },
      selectList: [],
      theData: {},
      tplTypeDict: {
        "fs": "飞书机器人", "fsapp": "飞书应用", "dd": "钉钉机器人", "ddapp": "钉钉应用",
        "wx": "企业微信机器人", "wxapp": "企业微信应用", "alydx": "阿里云短信", "alydh": "阿里云电话",
        "txdx": "腾讯云短信", "txdh": "腾讯云电话", "email": "邮件", "webhook": "Webhook"
      },
      aa: [{ label: "飞书机器人", value: "fs" }, { label: "飞书应用", value: "fsapp" },
      { label: "钉钉机器人", value: "dd" }, { label: "钉钉应用", value: "ddapp" },
      { label: "企业微信机器人", value: "wx" }, { label: "企业微信应用", value: "wxapp" },
      { label: "阿里云短信", value: "alydx" }, { label: "阿里云电话", value: "alydh" },
      { label: "腾讯云短信", value: "txdx" }, { label: "腾讯云电话", value: "txdh" },
      { label: "邮件", value: "email" }, { label: "Webhook", value: "webhook" }],
      columns: [
        {
          type: 'selection',
          key: '',
          width: 60
        },
        {
          title: '模版名称',
          key: 'name',
          minWidth: 150,
          maxWidth: 200
        },
        {
          title: '模版类型',
          key: 'type',
          sortable: true,
          minWidth: 115,
          maxWidth: 140,
          render: (h, params) => {
            let miaoshu = tplTypeDict[params.row.type]
            return h('div', [
              h('p', {
              }), miaoshu
            ]);
          }
        },
        {
          title: '模版用途',
          key: 'use',
          sortable: true,
          minWidth: 110,
          maxWidth: 150
        },
        {
          title: '路径',
          key: 'path',
          sortable: true,
          minWidth: 260
        },
        {
          title: '更新人',
          key: 'updated_by',
          sortable: true,
          minWidth: 105,
          maxWidth: 200
        },
        {
          title: '更新时间',
          key: 'updated_at',
          minWidth: 160,
          maxWidth: 220,
          render: (h, params) => {
            return h("div", formatDate(params.row.updated_at));
          },
        },
        {
          title: '操作',
          key: '',
          minWidth: 120,
          maxWidth: 180,
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.handleEdit(params.row)
                  }
                }
              }, '编辑'
              ),
              h('a', {
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => { this.handleDel(params.row) }
                }
              }, '删除'
              )
            ])
          }
        }
        //
      ],
      tableData: [
      ]
    }
  },
  methods: {
    async handleGetData () {
      // this.i.btnLoading = true
      this.initPara['filter_map'] = this.filter_map
      let res = await getNoticeTPL(this.initPara)
      if (res.data.code === 0) {
        this.tableData = res.data.result.data;
        this.i.pageTotal = res.data.result.count;
      } else {
        this.$Message.error(res.data.msg)
      }
      this.i.tableLoading = false
      this.initPara.cache = 'yes'
      setTimeout(() => {
        this.i.btnLoading = false
      }, 1000);
    },

    handleClose () {
      this.showEdit = false
      this.handleGetData()
    },
    handleAdd () {
      this.action = 'post'
      this.showEdit = true
      this.theData = Object.assign(this.$options.data().theData, this.initForm)
    },
    handleEdit (paramsRow) {
      this.action = 'put'
      this.showEdit = true
      this.theData = paramsRow
    },
    // 测试模板
    handleTestTPL (testData) {
      let testparams = { tpl: testData.name, type: testData.type, use: testData.use, url: testData.webhook, secret: testData.secret }
      let testBody = testData.test_json
      testNoticeTPL(testparams, testBody).then(res => {
        if (res.data.code == 0) {
          this.$Message.success('测试成功')
        } else {
          this.$Message.error(res.data.msg)
        }
      })
    },
    // 保存数据
    handleSaveData (saveData) {
      optNoticeTPL(saveData, this.action).then(res => {
        if (res.data.code == 0) {
          this.$Message.success('成功')
          this.handleGetData()
          if (this.action == 'post') {
            this.action = 'put'
            this.theData = res.data.result
          }
        } else {
          this.$Message.error(res.data.msg)
        }
      })
    },
    //
    handleDel (paramsRow) {
      this.$Modal.confirm({
        title: '提醒',
        content: `<p>删除《${paramsRow.name}》之后无法找回，请谨慎操作！</p>`,
        onOk: () => {
          setTimeout(() => {
            optNoticeTPL({ ids: [paramsRow.id] }, 'delete').then(res => {
              if (res.data.code === 0) {
                this.$Message.success('删除成功')
                this.handleGetData()
              } else {
                this.$Message.error('删除失败')
              }
            })
          }, 1000);
        },
        onCancel: () => { this.$Message.info('回头是岸~') }
      });
    },
    //
    handleRefresh () {
      this.initPara.pageNum = 1
      this.initPara.cache = 'no'
      this.handleGetData()
    },
    handleSearch () {
      this.initPara.pageNum = 1
      this.handleGetData()
    },
    handleSelect (val) {
      let selectIdList = []
      val.forEach(item => {
        selectIdList.push(item.id)
      })
      this.selectList = selectIdList
    },
    handleSort (column, key, order) {
      this.initPara.order = order === "asc" ? 'ascend' : 'ascend'
      this.initPara.field = key
      this.handleGetData()
    },
    handlerChangePage (value) {
      this.initPara.pageNum = value
      this.handleGetData()
    },
    handlePageSize (value) {
      this.initPara.pageNum = 1
      this.initPara.pageSize = value
      this.handleGetData()
    },
    //
    ...mapMutations([
      'setShowBusiness'
    ]),

  },
  watch: {
    selectList (val) {
      if (val.length > 0) {
        this.i.btnDisabled = false
      } else {
        this.i.btnDisabled = true
      }
    },
  },
  computed: {
    ...mapState({
      rules: state => state.user.rules,
    })
  },
  mounted () {
    this.setShowBusiness(false)
    this.handleGetData()
    if (this.$route.params.showEdit) {
      this.showEdit = true
    } else {
      this.showEdit = false
    }
  },
  beforeRouteLeave (to, from, next) {
    this.setShowBusiness(true)
    next()
  }

  //
}
</script>
<style lang="less" scoped>
.search-con {
  padding: 10px 0;

  .search {
    &-col {
      display: inline-block;
      width: 200px;
    }

    &-input {
      display: inline-block;
      width: 400px;
      margin-left: 5px;
    }

    &-btn {
      display: inline-block;
      margin-left: 2px;
      margin-right: 5px;
    }

    &-add-btn {
      margin-right: 10px;
    }
  }
}
</style>
