import axios from '@/libs/api.request'

export const getChannel = (params) => {
    return axios.request({
        url: '/api/noc/v1/channel',
        method: 'get',
        params
    })
}

export const getChannelD = (id) => {
    return axios.request({
        url: `/api/noc/v1/channel/${id}`,
        method: 'get'
    })
}



export const optChannel = (data, method) => {
    return axios.request({
        url: '/api/noc/v1/channel',
        method,
        data
    })
}

export const getUers = (params) => {
    return axios.request({
        url: '/api/noc/v1/user',
        method: 'get',
        params
    })
}
