<template>
  <div>
    <Card :bordered="false" dis-hover>
      <Col span="22" offset="1" style="padding: 20px 5px 30px">
      <span style="color: #464c5b; font-size: 16px; font-weight: bold;">新建消息模版</span>
      </Col>
      <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="85" :label-colon='true'>
        <Col span="19" offset="2">
        <FormItem label="模版名称" prop="name">
          <Input v-model="formValidate.name" maxlength="50" type="text" placeholder="请输入模版名称..."></Input>
        </FormItem>
        <Row :gutter="20">
          <Col span="12">
          <FormItem label="模版类型" prop="type">
            <Select v-model="formValidate.type" filterable placeholder="请选择...">
              <Option v-for="item in tplTypeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col span="12">
          <FormItem label="模版用途" prop="use">
            <Select v-model="formValidate.use" filterable placeholder="请选择...">
              <Option v-for="item in tplUsageList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>
          </Col>
        </Row>

        <FormItem label="模版内容" prop="content">
          <!-- <Input v-model="formValidate.content" type="textarea" maxlength="60000"
            :autosize="{ minRows: 10, maxRows: 25 }" placeholder="模版内容..."></Input> -->

          <b-ace-editor v-model="formValidate.content" lang="markdown" width="100%" height="300" theme="tomorrow"
            :font-size="fontSize"> </b-ace-editor>
        </FormItem>


        <FormItem label="测试JSON" prop="test_json">
          <!-- <Input v-model="formValidate.test_json" type="textarea" maxlength="60000"
            :autosize="{ minRows: 10, maxRows: 25 }" placeholder="消息协议JSON内容..."></Input> -->
          <b-ace-editor v-model="formValidate.test_json" lang="json" width="100%" height="300" theme="tomorrow"
            :font-size="fontSize">
          </b-ace-editor>

        </FormItem>
        <Row :gutter="20"
          v-if="formValidate.type == 'dd' || formValidate.type == 'fs' || formValidate.type == 'wx' || formValidate.type == 'webhook'">
          <Col span="15">
          <FormItem label="地址" prop="webhook">
            <Input v-model="formValidate.webhook" maxlength="120" type="text" placeholder="webhook..."></Input>
          </FormItem>
          </Col>
          <Col span="9">
          <FormItem label="密钥" prop="secret">
            <Input v-model="formValidate.secret" maxlength="100" type="text" placeholder="secret..."></Input>
          </FormItem>
          </Col>
        </Row>

        <FormItem>
          <Button type="success" :loading="btnLoading" ghost @click="handleTest">模板测试</Button>
          <Button type="warning" :loading="btnLoading" ghost @click="handleFormat"
            style="margin-left: 10px">格式化数据</Button>
          <Button type="primary" :loading="btnLoading" @click="handleSubmit('formValidate')"
            style="margin-left: 55px">保存</Button>
          <Button @click="handleCancel" style="margin-left: 15px">返回</Button>
        </FormItem>
        </Col>
      </Form>
    </Card>
  </div>
</template>
<script>
// import Editor from 'bin-ace-editor' // global filter
// require('brace/ext/language_tools') // language extension
// require('brace/mode/json')
// require('brace/snippets/json')
// require('brace/theme/monokai')
// // 注册组件后即可使用
// Vue.component(Editor.name, Editor)
export default {
  components: {},
  name: 'tplEdit',
  props: {
    formData: {
      type: Object,
      default: {}
    },
  },
  data () {
    return {
      fontSize: 13,
      //
      btnLoading: false,
      tplUsageList: [{ label: "天枢", value: "tianshu" }, { label: "Prometheus", value: "prometheus" },
      { label: "Grafana", value: "grafana" }, { label: "Webhook", value: "webhook" }],
      tplTypeList: [{ label: "飞书机器人", value: "fs" }, { label: "飞书应用", value: "fsapp" },
      { label: "钉钉机器人", value: "dd" }, { label: "钉钉应用", value: "ddapp" },
      { label: "企业微信机器人", value: "wx" }, { label: "企业微信应用", value: "wxapp" },
      { label: "阿里云短信", value: "alydx" }, { label: "阿里云电话", value: "alydh" },
      { label: "腾讯云短信", value: "txdx" }, { label: "腾讯云电话", value: "txdh" },
      { label: "邮件", value: "email" }, { label: "Webhook", value: "webhook" }],
      ruleValidate: {
        name: [{ required: true, message: '名称不能为空', trigger: 'blur' }],
        type: [{ required: true, message: '不能为空', trigger: 'change' }],
        use: [{ required: true, message: '不能为空', trigger: 'change' }],
        content: [{ required: true, message: '模版内容不能为空', trigger: 'blur' }],
      },
    }
  },
  methods: {
    handleCancel () {
      this.$emit('edit-close')
    },
    handleFormat () {
      this.formValidate.test_json = JSON.stringify(JSON.parse(this.formValidate.test_json), null, 2)
    },
    handleTest () {
      this.btnLoading = true
      setTimeout(() => {
        this.$emit('test-tpl', this.formValidate)
        this.btnLoading = false
      }, 1000)
    },
    //
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.btnLoading = true
          setTimeout(() => {
            this.$emit('save-data', this.formValidate)
            this.btnLoading = false
          }, 1000)
        } else {
          this.$Message.error('表单校验不通过!');
        }
      })
    },
    //

  },
  watch: {
    formValidate (value) {
    }
  },
  computed: {
    formValidate: function () {
      return this.formData
    }
  },
  //
  mounted () {
  }
}
</script>

