<template>
  <div>
    <Card :bordered="false" dis-hover>
      <Row>
        <Col span="22" offset="1" style="padding: 15px 5px">
        <Steps :current="current">
          <Step title="通道配置" content="这里是该步骤的描述信息"></Step>
          <Step title="固定模板" content="这里是该步骤的描述信息"></Step>
          <Step title="自定义模板（选填）" content="这里是该步骤的描述信息"></Step>
        </Steps>
        </Col>
      </Row>
      <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" label-position="left" :label-width="85"
        :label-colon='true'>
        <Row>
          <Col v-if="current == 0" span="19" offset="2">
          <FormItem label="通道用途" prop="use">
            <Select v-model="formValidate.use" filterable placeholder="请选择...">
              <Option v-for="item in tplUsageList" :value="item.value" :key="item.value" :label="item.label"></Option>
            </Select>
          </FormItem>

          <FormItem label="通道名称" prop="name">
            <Input v-model="formValidate.name" maxlength="50" type="text" placeholder="请输入通道名称..."></Input>
          </FormItem>

          <FormItem label="通知用户" prop="user">
            <Select v-model="formValidate.user" filterable multiple placeholder="请选择...">
              <Option v-for="item in userList" :value="item.user_id" :key="item.id"
                :label="`${item.username}(${item.nickname})`"></Option>
            </Select>
          </FormItem>
          <FormItem>
            <Alert show-icon>所有告警会默认通知相关业务运维负责人，如有配置需求请前往CMDB进行操作。</Alert>
          </FormItem>
          </Col>

          <Col v-else-if="current == 1" span="20" offset="1">
          <FormItem label="">

            <template v-for="(item, index) in formValidate.contact_points">
              {{ item.severity_desc }}
              <FormItem label="告警级别" v-if="item.type == 'alydh'">
                <CheckboxGroup v-model="item.severity">
                  <Checkbox label="fatal">致命</Checkbox>
                  <Checkbox label="error">严重</Checkbox>
                  <span style="padding-left: 15px;">空则视为不使用当前通知</span>
                </CheckboxGroup>
              </FormItem>
              <template v-if="item.type == 'alydh'">
                <Card dis-hover>
                  <FormItem label="">
                    <Select v-show="false" v-model="item.tpl_id" filterable placeholder="请选择..."
                      @on-change="handleChange(item.tpl_id, index)">
                      <template v-for="item in dhTpl">
                        <Option v-if="item.type == 'dh'" :value="item.id" :key="item.id">{{ item.name }} </Option>
                      </template>
                    </Select>
                    <Input v-model="item.content" maxlength="50" type="text" readonly placeholder=""></Input>
                  </FormItem>
                  <webhook-edit v-model="item.alert_webhooks"></webhook-edit>

                </Card>
              </template>

              <template v-else-if="item.type == 'fs' || item.type == 'dd'">
                <Card dis-hover>
                  <Row :gutter="20">
                    <Col span="24">
                    <FormItem label="选择模版"> <a @click="handleShowTPL(index, item)">点击查看/收起模版内容</a>
                      <Select v-model="item.tpl_id" filterable placeholder="请选择..."
                        @on-change="handleChange(item.tpl_id, index)">
                        <template v-if="item.type == 'dd' && ddTplList.length > 0">
                          <Option v-for="dd in ddTplList" :value="dd.id" :label="dd.name" :key="dd.id"> </Option>
                        </template>
                        <template v-else-if="item.type == 'fs' && fsTplList.length > 0">
                          <Option v-for="fs in fsTplList" :value="fs.id" :label="fs.name" :key="fs.id"> </Option>
                        </template>
                      </Select>
                    </FormItem>
                    </Col>

                    <Col span="24">
                    <FormItem style="padding-top: 10px;" v-show="item.show == 'yes'">
                      <Input v-model="item.content" type="textarea" maxlength="60000" readonly
                        :autosize="{ minRows: 10, maxRows: 25 }" placeholder="模版内容..."></Input>
                    </FormItem>
                    </Col>

                    <Col span="15" style="padding-top: 10px;">
                    <FormItem label="" prop="webhook">
                      <Input v-model="item.webhook" maxlength="120" type="text" placeholder="webhook..."></Input>
                    </FormItem>
                    </Col>
                    <Col span="9" style="padding-top: 10px;">
                    <FormItem label="" prop="secret">
                      <Input v-model="item.secret" maxlength="100" type="text" placeholder="secret..."></Input>
                    </FormItem>
                    </Col>
                  </Row>
                  <webhook-edit v-model="item.alert_webhooks"></webhook-edit>

                </Card>
              </template>

            </template>

          </FormItem>
          </Col>

          <Col v-else-if="current == 2" span="20" offset="1">
          <template>
            <FormItem v-for="(item, index) in formValidate.custom_items" :key="index + _uid">
              <div>
                <Row :gutter="20">
                  <Col span="22">
                  <Card dis-hover>
                    <Row :gutter="20">
                      <Col span="24">
                      <FormItem label="告警级别">
                        <CheckboxGroup v-model="item.severity">
                          <Checkbox label="fatal">致命</Checkbox>
                          <Checkbox label="error">严重</Checkbox>
                          <Checkbox label="warn">警告</Checkbox>
                          <!-- <Checkbox label="info">普通</Checkbox> -->
                          <span style="padding-left: 15px;">默认所有等级都会通知，空则视为不使用当前通知</span>
                        </CheckboxGroup>
                      </FormItem>
                      </Col>

                      <Col span="24">
                      <FormItem label="选择模版">
                        <Row>
                          <Col span="7">
                          <Select v-model="item.type" filterable @on-change="handleChangeC1(index)"
                            placeholder="请选择...">
                            <Option v-for="ctpl in customtplT" :value="ctpl.value" :label="ctpl.label"
                              :key="ctpl.value + _uid"></Option>
                          </Select>
                          </Col>
                          <Col span="17" style="padding-left: 10px;">
                          <Select v-model="item.tpl_id" filterable @on-change="handleChangeC(item.tpl_id, index)"
                            placeholder="请选择...">
                            <template v-if="item.type == 'dd'">
                              <template v-for="dd in ddTplList">
                                <Option v-if="dd.use != 'default'" :value="dd.id" :key="dd.id" :label="dd.name">
                                </Option>
                              </template>
                            </template>

                            <template v-else-if="item.type == 'fs'">
                              <template v-for="fs in fsTplList">
                                <Option v-if="fs.use != 'default'" :value="fs.id" :label="fs.name" :key="fs.id">
                                </Option>
                              </template>
                            </template>

                            <template v-else-if="item.type == 'fsapp'">
                              <template v-for="fs in fsAppTplList">
                                <Option v-if="fs.use != 'default'" :value="fs.id" :label="fs.name" :key="fs.id">
                                </Option>
                              </template>
                            </template>

                            <template v-else-if="item.type == 'ddapp'">
                              <template v-for="dd in ddAppTplList">
                                <Option v-if="dd.use != 'default'" :value="dd.id" :label="dd.name" :key="dd.id">
                                </Option>
                              </template>
                            </template>

                            <template v-else-if="item.type == 'wx'">
                              <template v-for="wx in wxTplList">
                                <Option v-if="wx.use != 'default'" :value="wx.id" :label="wx.name" :key="wx.id">
                                </Option>
                              </template>
                            </template>

                            <template v-else-if="item.type == 'wxapp'">
                              <template v-for="wx in wxAppTplList">
                                <Option v-if="wx.use != 'default'" :value="wx.id" :label="wx.name" :key="wx.id">
                                </Option>
                              </template>
                            </template>

                            <template v-else-if="item.type == 'alydx'">
                              <template v-for="dx in dxTpl">
                                <Option :value="dx.id" :label="dx.name">
                                </Option>
                              </template>
                            </template>

                            <template v-else-if="item.type == 'alydh'">
                              <template v-for="dh in dhTpl">
                                <Option :value="dh.id" :label="dh.name">
                                </Option>
                              </template>
                            </template>

                            <template v-else-if="item.type == 'email'">
                              <template v-for="email in emailList">
                                <Option :value="email.id" :label="email.name">
                                </Option>
                              </template>
                            </template>

                          </Select>
                          </Col>
                        </Row>
                      </FormItem>
                      </Col>

                      <Col span="24" style="padding-top: 10px;">
                      <FormItem label="模版内容">
                        <Row>
                          <Input v-model="item.content" type="textarea" maxlength="60000" readonly
                            :autosize="{ minRows: 10, maxRows: 25 }" placeholder="模版内容..."></Input>
                        </Row>
                      </FormItem>
                      </Col>

                      <Col span="24" style="padding-top: 10px;" v-if="item.type == 'fs' || item.type == 'dd' || item.type=='wx'" >
                      <FormItem label="webhook" prop="webhook">
                        <Row>
                          <Col span="15">
                          <Input v-model="item.webhook" maxlength="120" type="text" placeholder="webhook..."></Input>
                          </Col>
                          <Col span="9" style="padding-left: 10px;">
                          <Input v-model="item.secret" maxlength="100" type="text" placeholder="secret..."></Input>
                          </Col>
                        </Row>
                      </FormItem>
                      </Col>

                    </Row>
                    <webhook-edit v-model="item.alert_webhooks"></webhook-edit>

                  </Card>
                  </Col>

                  <Col span="2">
                  <a v-if="formValidate.custom_items.length !== 1" @click="handleRemove(index)">删除</a>
                  </Col>
                </Row>

              </div>
            </FormItem>

            <FormItem>
              <Row>
                <Col span="12">
                <Button type="dashed" long @click="handleAdd" icon="md-add">添加接入模板</Button>
                </Col>
                <p>您可以选择通知模版，若没有您想要选择的通知模版，也可以单击 <a @click.prevent="handleCreatTPL">创建通知模版</a>，去创建新的通知模版</p>
              </Row>
            </FormItem>
          </template>
          </Col>

          <Col span="19" :offset=offset>
          <FormItem>
            <ButtonGroup>
              <Button v-if="current != 0" :loading="btnLoading" @click="handleStep('back')">上一步</Button>
              <Button v-if="current != 2" :loading="btnLoading" @click="handleStep('next')">下一步</Button>
            </ButtonGroup>
            <template v-if="current == 0 && action == 'post'">
            </template>
            <template v-else>
              <Button type="primary" :loading="btnLoading" @click="handleSubmit('formValidate')"
                style="margin-left: 55px">{{ btnName }} </Button>
            </template>
            <Button @click="handleCancel" style="margin-left: 15px">返回</Button>
          </FormItem>
          </Col>
        </Row>
      </Form>

    </Card>
  </div>
</template>
<script>
import WebhookEdit from './webhook-edit.vue'

export default {

  components: {
    WebhookEdit
  },
  name: 'notificationEdit',
  props: {
    formData: {
      type: Object,
      default: {}
    },
    action: {
      type: String,
      default: 'post'
    },
    userList: {
      type: Array | String,
      default: []
    },
    tplList: {
      type: Array | String,
      default: []
    }
  },
  data () {
    return {
      offset: '2',
      current: 0,
      btnLoading: false,
      tplUsageList: [{ label: '天枢', value: 'tianshu' }, { label: 'Prometheus', value: 'prometheus' },
        { label: 'Grafana', value: 'grafana' }, { label: 'Webhook', value: 'webhook' }],
      tplTypeList: [{ label: '飞书机器人', value: 'fs' }, { label: '飞书应用', value: 'fsapp' },
        { label: '钉钉机器人', value: 'dd' }, { label: '钉钉应用', value: 'ddapp' },
        { label: '企业微信机器人', value: 'wx' }, { label: '企业微信应用', value: 'wxapp' },
        { label: '阿里云短信', value: 'alydx' }, { label: '阿里云电话', value: 'alydh' },
        { label: '腾讯云短信', value: 'txdx' }, { label: '腾讯云电话', value: 'txdh' },
        { label: '邮件', value: 'email' }],
      customtplT: [{ label: '飞书机器人', value: 'fs' }, { label: '飞书应用', value: 'fsapp' }, { label: '钉钉机器人', value: 'dd' },
        { label: '企业微信机器人', value: 'wx' },

        { label: '企业微信应用', value: 'wxapp' },

        { label: '钉钉应用', value: 'ddapp' },
        { label: '邮件', value: 'email' }, { label: '阿里云短信', value: 'alydx' }, { label: '阿里云电话', value: 'alydh' }],

      ruleValidate: {
        name: [{ required: true, message: '名称不能为空', trigger: 'blur' }],
        user: [{ required: true, type: 'array', message: '通知用户不能为空', trigger: 'change' }],
        use: [{ required: true, message: '不能为空', trigger: 'blur' }]
      }
    }
  },
  methods: {
    handleCancel () {
      this.$emit('edit-close')
    },
    handleStep (val) {
      if (val == 'back') {
        this.current = this.current - 1
        this.handleCheckCustomItems()
      } else {
        this.$refs['formValidate'].validate((valid) => {
          if (!valid) {
            this.$Message.error('表单校验不通过!')
          } else {
            this.current = this.current + 1
          }
        })
      }
    },
    handleShowTPL (index, item) {
      if (item.show == 'no') {
        let that = this
        let thisData = this.tplList.filter(ele => { return ele.id == item.tpl_id })[0]
        that.formValidate.contact_points[index]['content'] = thisData.content
        this.formValidate.contact_points[index]['show'] = 'yes'
      } else {
        this.formValidate.contact_points[index]['show'] = 'no'
      }
    },
    handleAdd () {
      this.index++
      if (this.formValidate.custom_items.length > 4) {
        this.$Notice.warning({
          title: '限制提示',
          desc: '添加自定义模板的个数不能超过五个',
          duration: 5
        })
        return
      }
      this.formValidate.custom_items.push({
        type: 'fs',
        severity: ['fatal', 'error', 'warn', 'info'],
        content: '',
        webhook: '',
        secret: '',
        index: this.index,
        status: 1
      })
    },
    handleRemove (index) {
      this.formValidate.custom_items.splice(index, 1)
    },
    handleCreatTPL () {
      const route = {
        name: 'M_Message',
        params: { 'showEdit': true }
      }
      this.$router.push(route)
    },
    handleChange (value, index) {
      let that = this
      that.formValidate.contact_points[index]['content'] = this.tplList.filter(item => { return item.id == value })[0].content
    },
    handleChangeC (value, index) {
      let that = this
      if (value && value != 'undefined') {
        that.formValidate.custom_items[index]['content'] = this.tplList.filter(item => { return item.id == value })[0].content
      }
    },
    handleChangeC1 (index) {
      let that = this
      that.formValidate.custom_items[index]['content'] = ''
    },
    handleGetContent () {
      let that = this
      that.formValidate.custom_items.forEach((element, index) => {
        if (this.tplList.filter(item => { return item.id == element.tpl_id }).length > 0) {
          that.formValidate.custom_items[index]['content'] = this.tplList.filter(item => { return item.id == element.tpl_id })[0].content
        }
      })
    },
    handleCheckCustomItems () {
      let that = this
      this.formValidate.custom_items = that.formValidate.custom_items.filter(item => { return item.tpl_id && item.tpl_id != 'undefined' })
    },
    //
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.btnLoading = true
          if (this.current == 1 && this.action === 'post') {
            delete this.formValidate.custom_items
          }
          this.formValidate.name = this.formValidate.name.trim()
          setTimeout(() => {
            this.$emit('save-data', this.formValidate)
            this.btnLoading = false
          }, 1000)
        } else {
          this.$Message.error('表单校验不通过!')
        }
      })
    }
    //

  },
  watch: {
    formValidate (value) {
    },
    current (val) {
      if (val == 0) {
        this.offset = '2'
      } else {
        this.offset = '1'
      }
      if (val == 2) {
        this.handleGetContent()
      }
    },
    dhTpl () { },
    dxTpl () { },
    wxTplList () { },
    wxAppTplList () { },
    ddTplList () { },
    ddAppTplList () { },
    fsTplList () { },
    fsAppTplList () { },
    emailList () { }
  },
  computed: {
    formValidate: function () {
      return this.formData
    },
    btnName: function () {
      if (this.action === 'post') {
        return '创建'
      } else {
        return '保存'
      }
    },
    emailList: function () {
      return this.tplList.filter(item => { return item.type == 'email' && item.use == this.formValidate.use })
    },
    wxTplList: function () {
      return this.tplList.filter(item => { return item.type == 'wx' && item.use == this.formValidate.use })
    },
    wxAppTplList: function () {
      return this.tplList.filter(item => { return item.type == 'wxapp' && item.use == this.formValidate.use })
    },
    ddTplList: function () {
      let that = this
      let ddTpls = this.tplList.filter(item => { return item.type == 'dd' && [this.formValidate.use, 'default'].indexOf(item.use) != -1 })
      let defaultDD = this.tplList.filter(item => { return item.type == 'dd' && item.use == 'tianshu' })[0]
      if (this.action === 'post') {
        // that.formValidate.contact_points[4]['tpl_id'] = defaultDD.id
        // that.formValidate.contact_points[4]['content'] = defaultDD.content
      }
      return ddTpls
    },
    fsTplList: function () {
      let that = this
      let fsTpls = this.tplList.filter(item => { return item.type == 'fs' && [this.formValidate.use, 'default'].indexOf(item.use) != -1 })
      let defaultFs = this.tplList.filter(item => { return item.type == 'fs' && item.use == 'tianshu' })[0]
      if (this.action === 'post' && defaultFs) {
        that.formValidate.contact_points[1].tpl_id = defaultFs.id
        that.formValidate.contact_points[1].content = defaultFs.content
      }
      return fsTpls
    },
    fsAppTplList: function () {
      let that = this
      let fsAppTpls = this.tplList.filter(item => { return item.type == 'fsapp' && [this.formValidate.use, 'default'].indexOf(item.use) != -1 })
      let defaultFsApp = this.tplList.filter(item => { return item.type == 'fsapp' && item.use == 'tianshu' })[0]
      // if (this.action === 'post') {
      //   that.formValidate.contact_points[2].tpl_id = defaultFsApp.id
      //   that.formValidate.contact_points[2]['content'] = defaultFsApp.content
      // }
      return fsAppTpls
    },
    ddAppTplList: function () {
      return this.tplList.filter(item => { return item.type == 'ddapp' && [this.formValidate.use, 'default'].indexOf(item.use) != -1 })
    },
    dxTpl: function () {
      return this.tplList.filter(item => { return item.type == 'alydx' && [this.formValidate.use, 'default'].indexOf(item.use) != -1 })
      // that.formValidate.contact_points[1] = {
      //   tpl_id: dxData.id,
      //   severity_desc: '短信（适用告警级别：致命 + 严重）',
      //   severity: ['fatal', 'error'],
      //   type: 'alydx',
      //   show: 'no',
      //   content: dxData.content,
      // }
    },
    dhTpl: function () {
      let that = this
      const dhData = this.tplList.filter((item) => {
        return item.type == 'alydh' && item.use == 'default'
      })[0]

      // 只有当 dhData 存在时才更新 contact_points
      if (dhData) {
        Object.assign(that.formValidate.contact_points[0], {
          tpl_id: dhData.id,
          severity_desc: '电话（适用告警级别：默认致命）',
          type: 'alydh',
          show: 'no',
          content: dhData.content
        })
      }

      return this.tplList.filter((item) => {
        return (
          item.type == 'alydh' &&
          [this.formValidate.use, 'default'].indexOf(item.use) != -1
        )
      })
    }
  },
  //
  mounted () {
  }
}
</script>
<style lang="less" scoped>
.alert-c {
  padding: 0px 0px;

  .alert {
    &-input {
      display: inline-block;
      min-width: 100px;
      max-width: 305px;
      margin-left: 0px;
    }

    &-select {
      display: inline-block;
      width: 150px;
      margin-left: 5px;
    }
  }
}

.alert-con {
  padding: 0px 0px 10px;

  .rule {
    &-col {
      display: inline-block;
      margin-left: 15px;
      // margin-right: 2px;
      width: 15px;
    }

    &-input {
      display: inline-block;
      width: 100px;
      margin-left: 5px;
    }

    // 第一个 无偏移
    &-input1 {
      display: inline-block;
      width: 85px;
    }

    &-input2 {
      display: inline-block;
      width: 220px;
    }

    &-select {
      display: inline-block;
      width: 85px;
      margin-left: 5px;
    }

    &-btn {
      display: inline-block;
      margin-left: 2px;
      margin-right: 5px;
    }

    &-btn-del {
      display: inline-block;
      margin-left: 10px;
      margin-top: 2px;
      margin-right: 5px;
    }
  }

}
</style>
