import axios from '@/libs/api.request'

export const getRoute = (params) => {
    return axios.request({
        url: '/api/noc/v1/router',
        method: 'get',
        params
    })
}



export const optRoute = (data, method) => {
    return axios.request({
        url: '/api/noc/v1/router',
        method,
        data
    })
}
